import React from 'react';
import commonStyles from '../pages/commonStyles.module.css';

export const renderOrderStatus = (status: string, styles: string) => {
    switch (status) {
        case 'done':
            return <p className={`${styles} ${commonStyles.statusDone}`}>{''}</p>;
        case 'pending':
            return <p className={styles}>{''}</p>;
        case 'created':
            return <p className={styles}>{''}</p>;
        case 'cancelled':
            return <p className={`${styles} ${commonStyles.statusCancelled}`}>{''}</p>;
        default:
            return <p className={styles}>{status}</p>
    }
}
